/**
 * Hook for tracking costs and displaying cost statistics
 */

import { useState, useEffect } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

/**
 * Manage cost tracking state and provide cost loading utilities
 * @returns {Object} Cost state and utilities
 */
export const useCostTracking = () => {
	const [totalCost, setTotalCost] = useState(0);
	const [loadingCosts, setLoadingCosts] = useState(false);
	const [showCostModal, setShowCostModal] = useState(false);
	const [costStats, setCostStats] = useState(null);
	const [costBreakdown, setCostBreakdown] = useState(null);

	/**
	 * Load total cost from API
	 */
	const loadTotalCost = async () => {
		setLoadingCosts(true);
		try {
			const response = await apiFetch({
				path: '/ai-media/v1/costs/total',
				method: 'GET',
			});
			setTotalCost(response.total_cost || 0);
		} catch (err) {
			// Failed to load total cost
		} finally {
			setLoadingCosts(false);
		}
	};

	/**
	 * Load detailed cost statistics and breakdown
	 */
	const loadCostStats = async () => {
		try {
			const [statsResponse, breakdownResponse] = await Promise.all([
				apiFetch({
					path: '/ai-media/v1/costs/stats',
					method: 'GET',
				}),
				apiFetch({
					path: '/ai-media/v1/costs/breakdown',
					method: 'GET',
				}),
			]);
			setCostStats(statsResponse);
			setCostBreakdown(breakdownResponse);
		} catch (err) {
			// Failed to load cost stats
		}
	};

	/**
	 * Add cost to total (e.g., after processing an image)
	 * @param {number} cost - Cost to add
	 */
	const addCost = (cost) => {
		setTotalCost(prev => {
			const newTotal = prev + cost;
			return newTotal;
		});
	};

	// Load costs on mount
	useEffect(() => {
		loadTotalCost();
	}, []);

	return {
		totalCost,
		loadingCosts,
		showCostModal,
		setShowCostModal,
		costStats,
		costBreakdown,
		loadTotalCost,
		loadCostStats,
		addCost,
	};
};
