/**
 * ImageThumbnail Component
 *
 * Displays a clickable thumbnail for quick image selection in Playground.
 */

const ImageThumbnail = ({ image, onClick, isSelected = false }) => {
	return (
		<div
			className={`image-thumbnail ${isSelected ? 'selected' : ''}`}
			onClick={() => onClick(image)}
			style={{
				position: 'relative',
				aspectRatio: '1',
				borderRadius: '4px',
				overflow: 'hidden',
				cursor: 'pointer',
				border: isSelected ? '3px solid #00a32a' : '2px solid #dcdcde',
				boxShadow: isSelected ? '0 0 0 2px rgba(0, 163, 42, 0.2)' : 'none',
				transition: 'all 0.2s ease',
				backgroundColor: '#f6f7f7'
			}}
			onMouseEnter={(e) => {
				if (!isSelected) {
					e.currentTarget.style.borderColor = '#007cba';
					e.currentTarget.style.transform = 'scale(1.05)';
				}
			}}
			onMouseLeave={(e) => {
				if (!isSelected) {
					e.currentTarget.style.borderColor = '#dcdcde';
					e.currentTarget.style.transform = 'scale(1)';
				}
			}}
		>
			<img
				src={image.url}
				alt={image.title}
				style={{
					width: '100%',
					height: '100%',
					objectFit: 'cover'
				}}
			/>
			{isSelected && (
				<div style={{
					position: 'absolute',
					top: '4px',
					right: '4px',
					width: '24px',
					height: '24px',
					borderRadius: '50%',
					backgroundColor: '#00a32a',
					display: 'flex',
					alignItems: 'center',
					justifyContent: 'center',
					color: 'white',
					fontSize: '14px',
					fontWeight: 'bold',
					boxShadow: '0 1px 3px rgba(0,0,0,0.3)'
				}}>
					
				</div>
			)}
			<div style={{
				position: 'absolute',
				bottom: 0,
				left: 0,
				right: 0,
				padding: '4px 6px',
				backgroundColor: 'rgba(0, 0, 0, 0.7)',
				color: 'white',
				fontSize: '11px',
				fontWeight: 500,
				whiteSpace: 'nowrap',
				overflow: 'hidden',
				textOverflow: 'ellipsis'
			}}>
				{image.filename}
			</div>
		</div>
	);
};

export default ImageThumbnail;
