import { Card, CardBody } from '@wordpress/components';
import ProviderCard from '../shared/ProviderCard';
import { getProviderList } from '../constants/providerConfig';

const ProvidersTab = ({ providers, setProviders, setShowModal, setModalMessage }) => {
	const updateProvider = (providerName, field, value) => {
		// If clearing API key and this provider is primary, show warning and prevent
		if (field === 'api_key' && value.trim() === '' && providers[providerName]?.primary) {
			setModalMessage('Cannot remove API key from the active provider. Please select a different provider first.');
			setShowModal(true);
			return;
		}

		setProviders({
			...providers,
			[providerName]: {
				...providers[providerName],
				[field]: value,
			},
		});
	};

	const setPrimaryProvider = (providerId) => {
		// Check if the selected provider has an API key
		if (!providers[providerId]?.api_key || providers[providerId].api_key.trim() === '') {
			setModalMessage('Please add an API key for this provider before selecting it as primary.');
			setShowModal(true);
			return;
		}

		// Update all providers - set the selected one as primary
		const updatedProviders = {};
		Object.keys(providers).forEach(key => {
			updatedProviders[key] = {
				...providers[key],
				primary: key === providerId,
			};
		});
		setProviders(updatedProviders);
	};

	// Get current primary provider, default to openai
	const primaryProvider = Object.keys(providers).find(key => providers[key]?.primary) || 'openai';

	const providerList = getProviderList();

	return (
		<div className="ai-media-providers-tab">
			<Card style={{ marginBottom: '20px', background: '#f0f6fc', borderLeft: '4px solid #2271b1' }}>
				<CardBody>
					<h3 style={{ marginTop: 0, marginBottom: '8px' }}>
						<span className="dashicons dashicons-info" style={{ fontSize: '20px', marginRight: '8px' }}></span>
						Select Your AI Provider
					</h3>
					<p style={{ margin: 0, color: '#1d2327' }}>
						Choose which AI service will analyze your images and generate metadata.
					</p>
				</CardBody>
			</Card>

			<div style={{ display: 'flex', flexWrap: 'wrap', gap: '16px' }}>
				{providerList.map((provider) => {
					const isPrimary = primaryProvider === provider.id;
					const hasApiKey = providers[provider.id]?.api_key?.length > 0;

					return (
						<ProviderCard
							key={provider.id}
							provider={provider}
							isPrimary={isPrimary}
							hasApiKey={hasApiKey}
							providers={providers}
							onUpdateProvider={updateProvider}
							onSetPrimary={setPrimaryProvider}
						/>
					);
				})}
			</div>
		</div>
	);
};

export default ProvidersTab;
