/**
 * SearchResultItem Component
 *
 * Displays a search result item with thumbnail, title, and metadata.
 */

const SearchResultItem = ({ image, onClick, isSelected = false }) => {
	return (
		<div
			className={`search-result-item ${isSelected ? 'selected' : ''}`}
			onClick={() => onClick(image)}
			style={{
				display: 'flex',
				alignItems: 'center',
				gap: '12px',
				padding: '10px',
				borderRadius: '4px',
				cursor: 'pointer',
				border: isSelected ? '2px solid #00a32a' : '1px solid #dcdcde',
				backgroundColor: isSelected ? '#f0f6fc' : '#fff',
				transition: 'all 0.15s ease',
				marginBottom: '6px'
			}}
			onMouseEnter={(e) => {
				if (!isSelected) {
					e.currentTarget.style.backgroundColor = '#f6f7f7';
					e.currentTarget.style.borderColor = '#007cba';
				}
			}}
			onMouseLeave={(e) => {
				if (!isSelected) {
					e.currentTarget.style.backgroundColor = '#fff';
					e.currentTarget.style.borderColor = '#dcdcde';
				}
			}}
		>
			{/* Thumbnail */}
			<div style={{
				width: '50px',
				height: '50px',
				flexShrink: 0,
				borderRadius: '3px',
				overflow: 'hidden',
				backgroundColor: '#f0f0f1',
				position: 'relative'
			}}>
				<img
					src={image.url}
					alt={image.title}
					style={{
						width: '100%',
						height: '100%',
						objectFit: 'cover'
					}}
				/>
				{isSelected && (
					<div style={{
						position: 'absolute',
						top: '2px',
						right: '2px',
						width: '18px',
						height: '18px',
						borderRadius: '50%',
						backgroundColor: '#00a32a',
						display: 'flex',
						alignItems: 'center',
						justifyContent: 'center',
						color: 'white',
						fontSize: '11px',
						fontWeight: 'bold',
						boxShadow: '0 1px 2px rgba(0,0,0,0.3)'
					}}>
						
					</div>
				)}
			</div>

			{/* Info */}
			<div style={{ flex: 1, minWidth: 0 }}>
				<div style={{
					fontWeight: 500,
					fontSize: '13px',
					color: '#1e1e1e',
					marginBottom: '3px',
					overflow: 'hidden',
					textOverflow: 'ellipsis',
					whiteSpace: 'nowrap'
				}}>
					{image.title}
				</div>
				<div style={{
					fontSize: '12px',
					color: '#646970',
					display: 'flex',
					gap: '8px',
					alignItems: 'center'
				}}>
					<span style={{
						display: 'inline-flex',
						alignItems: 'center',
						gap: '4px'
					}}>
						<span style={{ color: '#787c82' }}>ID:</span>
						<strong>{image.id}</strong>
					</span>
					<span style={{ color: '#dcdcde' }}></span>
					<span style={{
						overflow: 'hidden',
						textOverflow: 'ellipsis',
						whiteSpace: 'nowrap'
					}}>
						{image.filename}
					</span>
				</div>
			</div>

			{/* Arrow indicator */}
			<div style={{
				flexShrink: 0,
				color: '#646970',
				fontSize: '16px',
				opacity: isSelected ? 1 : 0,
				transition: 'opacity 0.15s ease'
			}}>
				
			</div>
		</div>
	);
};

export default SearchResultItem;
