import { useState, useEffect } from '@wordpress/element';
import { Card, CardHeader, CardBody, Button } from '@wordpress/components';
import PresetCard from '../shared/PresetCard';
import QualityImportanceSlider from '../shared/QualityImportanceSlider';
import QualityScorePreview from '../shared/QualityScorePreview';
import { QUALITY_PRESETS } from '../constants/presetConfig';
import { QUALITY_SCENARIOS } from '../constants/scenarioConfig';
import { QUALITY_FIELD_LABELS } from '../utils/settingsHelpers';

const QualityTab = ({ settings, setSettings, qualityWeights, setQualityWeights }) => {
	// Convert weights (0-1) to importance (0-100) for UI
	const [qualityImportance, setQualityImportance] = useState(() => ({
		alt: Math.round(qualityWeights.alt * 100),
		title: Math.round(qualityWeights.title * 100),
		caption: Math.round(qualityWeights.caption * 100),
		keywords: Math.round(qualityWeights.keywords * 100)
	}));

	// Sync importance with weights when weights change externally
	useEffect(() => {
		setQualityImportance({
			alt: Math.round(qualityWeights.alt * 100),
			title: Math.round(qualityWeights.title * 100),
			caption: Math.round(qualityWeights.caption * 100),
			keywords: Math.round(qualityWeights.keywords * 100)
		});
	}, [qualityWeights]);

	// Normalize importance to weights (sum = 1.0)
	const normalizeToWeights = (importance) => {
		const total = Object.values(importance).reduce((sum, val) => sum + val, 0);
		if (total === 0) return { alt: 0.25, title: 0.25, caption: 0.25, keywords: 0.25 };

		const weights = {};
		Object.keys(importance).forEach(key => {
			weights[key] = importance[key] / total;
		});
		return weights;
	};
	const [selectedPreset, setSelectedPreset] = useState('custom');
	const [showExplanation, setShowExplanation] = useState(false);

	// Apply preset
	const applyPreset = (presetId) => {
		if (QUALITY_PRESETS[presetId]) {
			const preset = QUALITY_PRESETS[presetId];
			setQualityImportance(preset.importance);
			const weights = normalizeToWeights(preset.importance);
			setQualityWeights(weights);
			setSettings({ ...settings, auto_approve_threshold: preset.autoApprove });
			setSelectedPreset(presetId);
		}
	};

	// Check if current settings match a preset
	const checkCurrentPreset = () => {
		for (const [id, preset] of Object.entries(QUALITY_PRESETS)) {
			const importanceMatch = Object.keys(preset.importance).every(
				key => Math.abs(qualityImportance[key] - preset.importance[key]) < 2
			);
			const thresholdMatch = Math.abs((settings.auto_approve_threshold || 0.85) - preset.autoApprove) < 0.01;

			if (importanceMatch && thresholdMatch) {
				return id;
			}
		}
		return 'custom';
	};

	// Detect and set initial preset on component mount
	useEffect(() => {
		const detectedPreset = checkCurrentPreset();
		setSelectedPreset(detectedPreset);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []); // Run only once on mount

	// Calculate quality preview scores
	const calculatePreviewScore = (fieldScores) => {
		let total = 0;
		const validFields = ['alt', 'title', 'caption', 'keywords'];
		for (const field of validFields) {
			if (fieldScores[field] !== undefined) {
				total += fieldScores[field] * (qualityWeights[field] || 0);
			}
		}
		return total;
	};

	// Handler for importance change
	const handleImportanceChange = (field, value) => {
		const newImportance = {
			...qualityImportance,
			[field]: value
		};
		setQualityImportance(newImportance);

		// Normalize and update weights
		const weights = normalizeToWeights(newImportance);
		setQualityWeights(weights);

		// Check if it matches a preset
		setSelectedPreset(checkCurrentPreset());
	};

	return (
		<div style={{ display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
			{/* Left Column: Presets & Weights */}
			<div style={{ flex: '1 1 calc(60% - 10px)', minWidth: '500px' }}>
				{/* Info Banner */}
				<Card style={{ marginBottom: '20px', background: '#f0f6fc', borderLeft: '4px solid #2271b1' }}>
					<CardBody>
						<h3 style={{ marginTop: 0, marginBottom: '8px', display: 'flex', alignItems: 'center', gap: '8px' }}>
							<span className="dashicons dashicons-info" style={{ fontSize: '20px' }}></span>
							Quality Scoring System
						</h3>
						<p style={{ margin: 0, color: '#1d2327', lineHeight: '1.6' }}>
							The quality score determines whether AI-generated metadata is automatically approved or requires review.
							Each field (ALT, Title, Caption, Keywords) contributes to the final score based on its <strong>SEO importance weight</strong>.
						</p>
						<Button
							variant="link"
							onClick={() => setShowExplanation(!showExplanation)}
							style={{ marginTop: '8px', padding: 0 }}
						>
							{showExplanation ? ' Hide' : ' Learn'} why these weights matter for SEO
						</Button>

						{showExplanation && (
							<div style={{
								marginTop: '12px',
								padding: '12px',
								background: '#fff',
								borderRadius: '4px',
								fontSize: '13px',
								lineHeight: '1.6'
							}}>
								<strong> ALT Text (40%):</strong> Most critical for Google Image Search and accessibility. Screen readers rely on ALT text.<br/>
								<strong> Title (30%):</strong> Important for social sharing, file organization, and structured data.<br/>
								<strong> Caption (20%):</strong> Adds context for users, improves engagement and time-on-page.<br/>
								<strong> Keywords (10%):</strong> Helps organize media library, minimal direct SEO impact.
							</div>
						)}
					</CardBody>
				</Card>

				{/* Preset Selection */}
				<Card style={{ marginBottom: '20px' }}>
					<CardHeader>
						<h3 style={{ margin: 0 }}>Quick Presets</h3>
					</CardHeader>
					<CardBody>
						<p style={{ marginTop: 0, marginBottom: '16px', color: '#646970' }}>
							Choose a preset configuration or customize your own weights below.
						</p>

						<div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(180px, 1fr))', gap: '12px', marginBottom: '16px' }}>
							{Object.entries(QUALITY_PRESETS).map(([id, preset]) => (
								<PresetCard
									key={id}
									preset={preset}
									presetId={id}
									isActive={selectedPreset === id}
									onApply={applyPreset}
								/>
							))}

							<button
								onClick={() => setSelectedPreset('custom')}
								style={{
									padding: '16px 12px',
									border: selectedPreset === 'custom' ? '2px solid #8c8f94' : '1px solid #dcdcde',
									borderRadius: '6px',
									background: selectedPreset === 'custom' ? '#f6f7f7' : '#fff',
									cursor: 'pointer',
									textAlign: 'center'
								}}
							>
								<div style={{ fontSize: '32px', marginBottom: '8px' }}></div>
								<div style={{ fontSize: '13px', fontWeight: '600', marginBottom: '4px' }}>
									Custom
								</div>
								<div style={{ fontSize: '11px', color: '#646970', lineHeight: '1.4' }}>
									Configure your own weights
								</div>
							</button>
						</div>
					</CardBody>
				</Card>

				{/* Importance Sliders */}
				<Card>
					<CardHeader>
						<h3 style={{ margin: 0 }}>Field Importance</h3>
					</CardHeader>
					<CardBody>
						{['alt', 'title', 'caption', 'keywords'].map((field) => (
							<QualityImportanceSlider
								key={field}
								field={field}
								value={qualityImportance[field]}
								onChange={(value) => handleImportanceChange(field, value)}
								labels={QUALITY_FIELD_LABELS}
							/>
						))}

					</CardBody>
				</Card>

				{/* SEO Impact Info */}
				<Card style={{ background: '#f0f6fc', borderLeft: '4px solid #2271b1' }}>
					<CardBody>
						<h4 style={{ marginTop: 0, marginBottom: '12px', fontSize: '14px' }}>
							 SEO Impact Guide
						</h4>
						<div style={{ fontSize: '12px', lineHeight: '1.6', color: '#1d2327' }}>
							<p style={{ marginTop: 0 }}>
								<strong>ALT Text</strong> is the most important field because:
							</p>
							<ul style={{ marginLeft: '20px', marginBottom: '12px' }}>
								<li>Primary ranking factor for Google Image Search</li>
								<li>Required for web accessibility (WCAG compliance)</li>
								<li>Screen readers rely on it exclusively</li>
							</ul>

							<p>
								<strong>Title</strong> matters for:
							</p>
							<ul style={{ marginLeft: '20px', marginBottom: '12px' }}>
								<li>Social media sharing (Open Graph)</li>
								<li>Structured data (Schema.org)</li>
								<li>Image filename SEO</li>
							</ul>

							<p style={{ marginBottom: 0 }}>
								<strong>Caption</strong> and <strong>Keywords</strong> provide supplementary value through content enrichment and internal organization.
							</p>
						</div>
					</CardBody>
				</Card>
			</div>

			{/* Right Column: Preview & Stats */}
			<div style={{ flex: '1 1 calc(40% - 10px)', minWidth: '350px' }}>
				<QualityScorePreview
					scenarios={QUALITY_SCENARIOS}
					qualityWeights={qualityWeights}
					autoApproveThreshold={settings.auto_approve_threshold || 0.85}
					calculateScore={calculatePreviewScore}
				/>
			</div>
		</div>
	);
};

export default QualityTab;
