/**
 * Get primary provider from providers object
 */
export const getPrimaryProvider = (providers, defaultProvider = 'openai') => {
	return Object.keys(providers).find(key => providers[key]?.primary) || defaultProvider;
};

/**
 * Get provider help URL
 */
export const getProviderHelpUrl = (providerName) => {
	const urls = {
		'OpenAI (ChatGPT)': 'platform.openai.com',
		'Anthropic (Claude)': 'console.anthropic.com',
		'Google (Gemini)': 'console.cloud.google.com'
	};
	return urls[providerName] || '';
};

