import { render, useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { Modal, Button } from '@wordpress/components';
import AdminNotice from './components/shared/AdminNotice';

// Import tabs
import ProvidersTab from './components/settings/tabs/ProvidersTab';
import PromptTab from './components/settings/tabs/PromptTab';
import ConfigurationTab from './components/settings/tabs/ConfigurationTab';
import ContextTab from './components/settings/tabs/ContextTab';
import QualityTab from './components/settings/tabs/QualityTab';
import PlaygroundTab from './components/settings/tabs/PlaygroundTab';

// Import shared components
import TabsContainer from './components/settings/shared/TabsContainer';

// Import constants
import { SETTINGS_TABS } from './components/settings/constants/settingsTabs';

import '../css/settings.css';

const Settings = () => {
	// State management
	const [settings, setSettings] = useState(window.aiMediaSEO?.settings || {});
	const [providers, setProviders] = useState(window.aiMediaSEO?.providers || {});
	const [qualityWeights, setQualityWeights] = useState(
		window.aiMediaSEO?.quality_weights || {
			alt: 0.40,
			title: 0.30,
			caption: 0.20,
			keywords: 0.10
		}
	);
	const [saving, setSaving] = useState(false);
	const [saveMessage, setSaveMessage] = useState(null);
	const [activeTab, setActiveTab] = useState('providers');
	const [showModal, setShowModal] = useState(false);
	const [modalMessage, setModalMessage] = useState('');

	// Validation
	const siteContextLength = (settings.site_context || '').length;
	const isSiteContextOverLimit = siteContextLength > 1500;

	// Save function
	const saveSettings = async () => {
		try {
			setSaving(true);
			setSaveMessage(null);

			const response = await apiFetch({
				path: '/ai-media/v1/settings',
				method: 'POST',
				data: {
					settings: settings,
					providers: providers,
					quality_weights: qualityWeights,
				},
			});

			// Update global window object so values persist when navigating between tabs
			if (window.aiMediaSEO && response.updated) {
				if (response.updated.settings) {
					window.aiMediaSEO.settings = response.updated.settings;
				}
				if (response.updated.providers) {
					window.aiMediaSEO.providers = response.updated.providers;
				}
				if (response.updated.quality_weights) {
					window.aiMediaSEO.quality_weights = response.updated.quality_weights;
				}
			}

			setSaveMessage({
				type: 'success',
				message: response.message || 'Settings saved successfully!'
			});
		} catch (err) {
			setSaveMessage({
				type: 'error',
				message: 'Error saving settings: ' + err.message
			});
		} finally {
			setSaving(false);
		}
	};

	return (
		<div className="ai-media-settings">
			<style>
				{`
					.ai-media-settings .components-form-toggle.is-checked .components-form-toggle__thumb {
						left: 6px !important;
					}
				`}
			</style>

			<TabsContainer
				tabs={SETTINGS_TABS}
				activeTab={activeTab}
				onTabChange={setActiveTab}
				onSave={saveSettings}
				saving={saving}
				disabled={isSiteContextOverLimit}
				disabledTitle={isSiteContextOverLimit ? 'Site Context exceeds 1500 character limit' : ''}
			/>

			{saveMessage && (
				<AdminNotice
					notice={saveMessage}
					onDismiss={() => setSaveMessage(null)}
				/>
			)}

			{showModal && (
				<Modal
					title="API Key Required"
					onRequestClose={() => setShowModal(false)}
				>
					<p>{modalMessage}</p>
					<Button variant="primary" onClick={() => setShowModal(false)}>
						OK
					</Button>
				</Modal>
			)}

			<div className="ai-media-settings-content">
				{activeTab === 'providers' && (
					<ProvidersTab
						providers={providers}
						setProviders={setProviders}
						setShowModal={setShowModal}
						setModalMessage={setModalMessage}
					/>
				)}

				{activeTab === 'prompt' && (
					<PromptTab
						settings={settings}
						setSettings={setSettings}
					/>
				)}

				{activeTab === 'context' && (
					<ContextTab
						settings={settings}
						setSettings={setSettings}
					/>
				)}

				{activeTab === 'configuration' && (
					<ConfigurationTab
						settings={settings}
						setSettings={setSettings}
					/>
				)}

				{activeTab === 'quality' && (
					<QualityTab
						settings={settings}
						setSettings={setSettings}
						qualityWeights={qualityWeights}
						setQualityWeights={setQualityWeights}
					/>
				)}

				{activeTab === 'playground' && (
					<PlaygroundTab preselectedImageId={null} />
				)}
			</div>
		</div>
	);
};

// Initialize settings when DOM is ready
document.addEventListener('DOMContentLoaded', () => {
	const root = document.getElementById('ai-media-settings-root');
	if (root) {
		render(<Settings />, root);
	}
});
