import { Card, CardHeader, CardBody, TextControl, SelectControl } from '@wordpress/components';

const ProviderCard = ({
	provider,
	isPrimary,
	hasApiKey,
	providers,
	onUpdateProvider,
	onSetPrimary
}) => {
	return (
		<Card
			style={{
				flex: '0 0 calc(50% - 8px)',
				maxWidth: 'calc(50% - 8px)',
				marginBottom: '0',
				border: isPrimary ? '2px solid #2271b1' : '1px solid #dcdcde',
				position: 'relative',
				boxSizing: 'border-box'
			}}
		>
			<CardHeader style={{
				background: isPrimary ? '#f0f6fc' : 'transparent',
				borderBottom: isPrimary ? '1px solid #2271b1' : '1px solid #dcdcde'
			}}>
				<div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', width: '100%' }}>
					<div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
						<style>
							{`
								.provider-radio-button:checked::before {
									margin: 0.31rem !important;
								}
							`}
						</style>
						<input
							type="radio"
							name="primary_provider"
							value={provider.id}
							checked={isPrimary}
							onChange={() => onSetPrimary(provider.id)}
							className="provider-radio-button"
							style={{
								width: '20px',
								height: '20px',
								cursor: 'pointer'
							}}
						/>
						<div>
							<h3 style={{ margin: 0, display: 'flex', alignItems: 'center', gap: '8px' }}>
								{provider.name}
								{isPrimary && (
									<span style={{
										background: '#2271b1',
										color: '#fff',
										padding: '2px 8px',
										borderRadius: '3px',
										fontSize: '11px',
										fontWeight: 'bold',
										textTransform: 'uppercase'
									}}>
										Active
									</span>
								)}
							</h3>
							<p style={{ margin: '4px 0 0 0', color: '#757575', fontSize: '13px' }}>
								{provider.description}
							</p>
						</div>
					</div>
					{hasApiKey && (
						<span className="dashicons dashicons-yes-alt" style={{
							color: '#00a32a',
							fontSize: '24px'
						}} title="API Key configured"></span>
					)}
				</div>
			</CardHeader>
			<CardBody>
				<TextControl
					label="API Key"
					type="password"
					value={providers[provider.id]?.api_key || ''}
					onChange={(value) => onUpdateProvider(provider.id, 'api_key', value)}
					help={`Get your API key from ${provider.helpUrl || (provider.name === 'OpenAI (ChatGPT)' ? 'platform.openai.com' : provider.name === 'Anthropic (Claude)' ? 'console.anthropic.com' : 'console.cloud.google.com')}`}
					placeholder="sk-..."
				/>

				{Object.keys(provider.models).length > 1 && (
					<SelectControl
						label="Model"
						value={providers[provider.id]?.model || Object.keys(provider.models)[0]}
						options={Object.entries(provider.models).map(([value, label]) => ({
							label,
							value,
						}))}
						onChange={(value) => onUpdateProvider(provider.id, 'model', value)}
						help="Select which AI model to use for image analysis"
						style={{ minHeight: '38px' }}
					/>
				)}
			</CardBody>
		</Card>
	);
};

export default ProviderCard;
