import { Card, CardHeader, CardBody, SelectControl, ToggleControl, TextControl } from '@wordpress/components';

const ConfigurationTab = ({ settings, setSettings }) => {
	// Get available image sizes from localized data
	const availableSizes = window.aiMediaSEO?.image_sizes || [];

	// Prepare options for SelectControl
	const sizeOptions = availableSizes.map(size => ({
		label: size.label,
		value: size.value
	}));

	return (
		<div style={{ display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
			{/* Left Column: Processing Settings */}
			<div style={{ flex: '1 1 calc(50% - 10px)', minWidth: '400px' }}>
				<Card>
					<CardHeader>
						<h3 style={{ margin: 0, display: 'flex', alignItems: 'center', gap: '8px' }}>
							<span className="dashicons dashicons-admin-settings" style={{ fontSize: '20px' }}></span>
							Processing Settings
						</h3>
					</CardHeader>
					<CardBody>
						<div style={{ marginBottom: '20px' }}>
							<SelectControl
								label="Image Size for AI Analysis"
								value={settings.image_size_for_ai || 'large'}
								options={sizeOptions}
								onChange={(value) => setSettings({ ...settings, image_size_for_ai: value })}
								help=""
							/>
							<p style={{
								margin: '8px 0 0 0',
								fontSize: '12px',
								color: '#646970',
								fontStyle: 'italic',
								lineHeight: '1.5'
							}}>
								Choose which WordPress image size to send to AI providers for analysis. Smaller sizes reduce API costs and processing time, while larger sizes may provide more detailed analysis. Recommended: <strong>Large</strong> (typically 1024px) for best balance between quality and cost.
							</p>
						</div>

						<div style={{ marginBottom: '20px' }}>
							<ToggleControl
								label="Enable Image Size Fallback"
								checked={settings.enable_image_size_fallback ?? true}
								onChange={(value) => setSettings({ ...settings, enable_image_size_fallback: value })}
								help=""
							/>
							<p style={{
								margin: '8px 0 0 0',
								fontSize: '12px',
								color: '#646970',
								fontStyle: 'italic',
								lineHeight: '1.5'
							}}>
								If enabled and your selected image size doesn't exist for a particular image, the plugin will automatically try to find another available size. If disabled, the original full-size image will be used instead, which may increase API costs.
							</p>
						</div>

						<div style={{ marginBottom: '20px' }}>
							<ToggleControl
								label="Enable Auto-processing"
								checked={settings.enable_auto_process || false}
								onChange={(value) => setSettings({ ...settings, enable_auto_process: value })}
								help=""
							/>
							<p style={{
								margin: '8px 0 0 0',
								fontSize: '12px',
								color: '#646970',
								fontStyle: 'italic',
								lineHeight: '1.5'
							}}>
								When enabled, newly uploaded images will be automatically processed by AI to generate SEO metadata (ALT text, caption, title). This is convenient but will consume your API credits automatically. <strong>Recommended:</strong> Leave disabled and process images manually or in batches to maintain better control over API costs.
							</p>
						</div>

						<div>
							<TextControl
								label="ALT Text Maximum Length"
								type="number"
								value={settings.alt_max_length || 125}
								onChange={(value) => setSettings({ ...settings, alt_max_length: parseInt(value) })}
								help=""
							/>
							<p style={{
								margin: '8px 0 0 0',
								fontSize: '12px',
								color: '#646970',
								fontStyle: 'italic',
								lineHeight: '1.5'
							}}>
								Maximum number of characters allowed in ALT text. Google recommends keeping ALT text concise (typically under 125 characters) for optimal accessibility and SEO. The AI will generate ALT text that respects this limit while remaining descriptive and keyword-rich.
							</p>
						</div>
					</CardBody>
				</Card>
			</div>
		</div>
	);
};

export default ConfigurationTab;
