/**
 * WordPress Admin Notice component
 * Displays dismissible notices (success, error, warning, info)
 */

/**
 * AdminNotice component
 * @param {Object} props - Component props
 * @param {Object} props.notice - Notice object
 * @param {string} props.notice.type - Notice type: 'success', 'error', 'warning', 'info'
 * @param {string} props.notice.message - Notice message to display
 * @param {Function} props.onDismiss - Callback when notice is dismissed
 * @returns {JSX.Element|null} Rendered notice or null if no notice
 */
const AdminNotice = ({ notice, onDismiss }) => {
	if (!notice) {
		return null;
	}

	return (
		<div className={`notice notice-${notice.type} is-dismissible`} style={{ margin: '0 0 20px 0' }}>
			<p>{notice.message}</p>
			<button
				type="button"
				className="notice-dismiss"
				onClick={onDismiss}
			>
				<span className="screen-reader-text">Dismiss this notice.</span>
			</button>
		</div>
	);
};

export default AdminNotice;
