/**
 * AnalysisResults Component
 *
 * Displays AI analysis results with metadata, costs, and performance metrics.
 */

import { useState } from '@wordpress/element';
import { Card, CardBody, CardHeader } from '@wordpress/components';

const AnalysisResults = ({ result }) => {
	const [showRawJson, setShowRawJson] = useState(false);

	// Get auto-approve threshold from global settings
	const autoApproveThreshold = window.aiMediaSEO?.settings?.auto_approve_threshold || 0.85;

	const formatCost = (cost) => {
		return `$${cost.toFixed(6)}`;
	};

	// Get score color based on threshold
	const getScoreColor = (score) => {
		if (score >= autoApproveThreshold) {
			return '#46b450'; // Green - Auto-approved
		} else if (score >= 0.70) {
			return '#ffb900'; // Yellow - Needs review
		} else {
			return '#dc3232'; // Red - Low quality
		}
	};

	return (
		<div style={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
			{/* Metadata Section */}
			<Card>
				<CardHeader style={{ background: '#f0f6fc', borderBottom: '1px solid #2271b1' }}>
					<h4 style={{ margin: 0 }}> Generated Metadata</h4>
				</CardHeader>
				<CardBody>
					<div style={{ display: 'grid', gridTemplateColumns: '150px 1fr', gap: '10px' }}>
						<strong>Provider:</strong>
						<span>{result.provider || 'N/A'}</span>

						<strong>Model:</strong>
						<span>{result.model || 'N/A'}</span>

						<strong>Quality Score:</strong>
						<span style={{
							color: result.score ? getScoreColor(result.score) : '#999',
							fontWeight: 'bold'
						}}>
							{result.score ? `${Math.round(result.score * 100)}%` : 'N/A'}
						</span>

						<strong>Status:</strong>
						<span>{result.status || 'N/A'}</span>
					</div>

					{result.metadata && (
						<div style={{ marginTop: '20px' }}>
							<h5>Generated Content:</h5>

							{result.metadata.alt && (
								<div style={{ marginBottom: '15px' }}>
									<strong>ALT Text:</strong>
									<div style={{
										padding: '10px',
										background: '#f6f7f7',
										borderLeft: '3px solid #2271b1',
										marginTop: '5px'
									}}>
										{result.metadata.alt}
									</div>
								</div>
							)}

							{result.metadata.caption && (
								<div style={{ marginBottom: '15px' }}>
									<strong>Caption:</strong>
									<div style={{
										padding: '10px',
										background: '#f6f7f7',
										borderLeft: '3px solid #2271b1',
										marginTop: '5px'
									}}>
										{result.metadata.caption}
									</div>
								</div>
							)}

							{result.metadata.title && (
								<div style={{ marginBottom: '15px' }}>
									<strong>Title:</strong>
									<div style={{
										padding: '10px',
										background: '#f6f7f7',
										borderLeft: '3px solid #2271b1',
										marginTop: '5px'
									}}>
										{result.metadata.title}
									</div>
								</div>
							)}

							{result.metadata.keywords && result.metadata.keywords.length > 0 && (
								<div>
									<strong>Keywords:</strong>
									<div style={{ marginTop: '5px' }}>
										{result.metadata.keywords.map((keyword, i) => (
											<span key={i} style={{
												display: 'inline-block',
												padding: '4px 8px',
												margin: '2px',
												background: '#f0f6fc',
												border: '1px solid #2271b1',
												borderRadius: '3px',
												fontSize: '12px'
											}}>
												{keyword}
											</span>
										))}
									</div>
								</div>
							)}
						</div>
					)}
				</CardBody>
			</Card>

			{/* Cost Section */}
			{result.costs && (
				<Card>
					<CardHeader style={{ background: '#f0f6fc', borderBottom: '1px solid #2271b1' }}>
						<h4 style={{ margin: 0 }}> Cost Analysis</h4>
					</CardHeader>
					<CardBody>
						<div style={{ display: 'grid', gridTemplateColumns: '200px 1fr', gap: '10px' }}>
							<strong>Input Tokens:</strong>
							<span>
								{result.costs.input_tokens?.toLocaleString() || 'N/A'}
								{result.costs.estimated_input && ' (estimated)'}
							</span>

							<strong>Output Tokens:</strong>
							<span>{result.costs.output_tokens?.toLocaleString() || 'N/A'}</span>

							<strong>Input Cost:</strong>
							<span>{result.costs.input_cost ? formatCost(result.costs.input_cost) : 'N/A'}</span>

							<strong>Output Cost:</strong>
							<span>{result.costs.output_cost ? formatCost(result.costs.output_cost) : 'N/A'}</span>

							<strong style={{ fontSize: '16px' }}>Total Cost:</strong>
							<span style={{ fontSize: '16px', fontWeight: 'bold', color: '#2271b1' }}>
								{result.costs.total_cost ? formatCost(result.costs.total_cost) : 'N/A'}
							</span>
						</div>
					</CardBody>
				</Card>
			)}

			{/* Raw Response */}
			<Card>
				<CardHeader style={{ background: '#f6f7f7' }}>
					<div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
						<h4 style={{ margin: 0 }}> Raw Response</h4>
						<button
							onClick={() => setShowRawJson(!showRawJson)}
							style={{
								padding: '4px 12px',
								border: '1px solid #2271b1',
								background: 'white',
								borderRadius: '3px',
								cursor: 'pointer'
							}}
						>
							{showRawJson ? 'Hide' : 'Show'} JSON
						</button>
					</div>
				</CardHeader>
				{showRawJson && (
					<CardBody>
						<pre style={{
							background: '#f6f7f7',
							padding: '15px',
							borderRadius: '4px',
							overflow: 'auto',
							maxHeight: '400px',
							fontSize: '12px',
							fontFamily: 'Consolas, Monaco, monospace'
						}}>
							{JSON.stringify(result, null, 2)}
						</pre>
					</CardBody>
				)}
			</Card>
		</div>
	);
};

export default AnalysisResults;
