/**
 * ContextViewer Component
 *
 * Displays context data with syntax highlighting and collapsible sections.
 */

import { useState } from '@wordpress/element';

const ContextViewer = ({ context }) => {
	const [collapsed, setCollapsed] = useState({});

	const toggleCollapse = (key) => {
		setCollapsed(prev => ({
			...prev,
			[key]: !prev[key],
		}));
	};

	const renderValue = (value, key, level = 0) => {
		if (value === null || value === undefined) {
			return <span style={{ color: '#999' }}>null</span>;
		}

		if (typeof value === 'boolean') {
			return <span style={{ color: '#0066cc' }}>{value.toString()}</span>;
		}

		if (typeof value === 'number') {
			return <span style={{ color: '#990000' }}>{value}</span>;
		}

		if (typeof value === 'string') {
			return <span style={{ color: '#008000' }}>"{value}"</span>;
		}

		if (Array.isArray(value)) {
			if (value.length === 0) {
				return <span style={{ color: '#999' }}>[]</span>;
			}

			const isCollapsed = collapsed[key];

			return (
				<div>
					<span
						onClick={() => toggleCollapse(key)}
						style={{ cursor: 'pointer', userSelect: 'none' }}
					>
						{isCollapsed ? '' : ''} [{value.length} items]
					</span>
					{!isCollapsed && (
						<div style={{ marginLeft: '20px' }}>
							{value.map((item, index) => (
								<div key={index}>
									{index}: {renderValue(item, `${key}.${index}`, level + 1)}
								</div>
							))}
						</div>
					)}
				</div>
			);
		}

		if (typeof value === 'object') {
			const keys = Object.keys(value);
			if (keys.length === 0) {
				return <span style={{ color: '#999' }}>{'{}'}</span>;
			}

			const isCollapsed = collapsed[key];

			return (
				<div>
					<span
						onClick={() => toggleCollapse(key)}
						style={{ cursor: 'pointer', userSelect: 'none' }}
					>
						{isCollapsed ? '' : ''} {'{'}...{'}'}
					</span>
					{!isCollapsed && (
						<div style={{ marginLeft: '20px' }}>
							{keys.map(k => (
								<div key={k}>
									<span style={{ color: '#881391' }}>"{k}"</span>: {renderValue(value[k], `${key}.${k}`, level + 1)}
								</div>
							))}
						</div>
					)}
				</div>
			);
		}

		return <span>{String(value)}</span>;
	};

	return (
		<div style={{
			background: '#f6f7f7',
			border: '1px solid #dcdcde',
			borderRadius: '4px',
			padding: '15px',
			fontFamily: 'Consolas, Monaco, monospace',
			fontSize: '13px',
			lineHeight: '1.6',
			maxHeight: '600px',
			overflow: 'auto'
		}}>
			<div>
				{/* Global Settings */}
				{context.global_settings && (
					<div style={{ marginBottom: '20px' }}>
						<div style={{ fontWeight: 'bold', marginBottom: '8px', color: '#2271b1' }}>
							 Global Settings:
						</div>
						<div style={{ marginLeft: '20px' }}>
							{renderValue(context.global_settings, 'global_settings')}
						</div>
					</div>
				)}

				{/* Context Data */}
				{context.context && (
					<div style={{ marginBottom: '20px' }}>
						<div style={{ fontWeight: 'bold', marginBottom: '8px', color: '#2271b1' }}>
							 Context Data:
						</div>
						<div style={{ marginLeft: '20px' }}>
							{renderValue(context.context, 'context')}
						</div>
					</div>
				)}

				{/* Context Score */}
				{context.context_score !== undefined && (
					<div style={{ marginBottom: '20px' }}>
						<div style={{ fontWeight: 'bold', marginBottom: '8px', color: '#2271b1' }}>
							 Context Score:
						</div>
						<div style={{ marginLeft: '20px' }}>
							<span style={{ color: '#990000' }}>{context.context_score.toFixed(2)}</span>
						</div>
					</div>
				)}

				{/* Final Prompt */}
				{context.final_prompt && (
					<div>
						<div style={{ fontWeight: 'bold', marginBottom: '8px', color: '#2271b1' }}>
							 Final Prompt ({context.used_variant || 'standard'} variant):
						</div>
						<pre style={{
							background: '#fff',
							border: '1px solid #dcdcde',
							borderRadius: '4px',
							padding: '12px',
							marginTop: '8px',
							marginLeft: '20px',
							overflow: 'auto',
							whiteSpace: 'pre-wrap',
							fontSize: '12px',
							maxHeight: '400px'
						}}>
							{context.final_prompt}
						</pre>
					</div>
				)}
			</div>
		</div>
	);
};

export default ContextViewer;
