import { Modal, Button, Spinner } from '@wordpress/components';
import { useState, useEffect } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

const PromptPreviewModal = ({ isOpen, onClose, variant, settings, selectedVariant }) => {
	const [prompt, setPrompt] = useState('');
	const [loading, setLoading] = useState(false);
	const [error, setError] = useState(null);

	useEffect(() => {
		if (!isOpen) return;

		const fetchPrompt = async () => {
			setLoading(true);
			setError(null);
			try {
				const response = await apiFetch({
					path: '/ai-media/v1/settings/prompt-preview',
					method: 'POST',
					data: {
						variant: selectedVariant,
						language: settings.primary_language || 'en'
					}
				});
				setPrompt(response.prompt);
			} catch (err) {
				setError(err.message || 'Failed to load prompt preview');
			} finally {
				setLoading(false);
			}
		};

		fetchPrompt();
	}, [isOpen, selectedVariant, settings.primary_language, settings.ai_role, settings.site_context, settings.alt_max_length]);

	if (!isOpen) return null;

	return (
		<Modal
			title={`${variant.name} Prompt Structure Preview`}
			onRequestClose={onClose}
			style={{ maxWidth: '800px', width: '90vw' }}
			shouldCloseOnClickOutside={false}
			isDismissible={true}
		>
			<div style={{ fontFamily: 'monospace', fontSize: '12px', lineHeight: '1.6' }}>
				{/* Variant Badge */}
				<div style={{
					background: variant.color,
					color: '#fff',
					padding: '8px 16px',
					borderRadius: '4px',
					marginBottom: '16px',
					fontSize: '13px',
					fontWeight: '600',
					textAlign: 'center'
				}}>
					{variant.name} Variant  {variant.totalTokens}
				</div>

				<div style={{ background: '#f0f0f0', padding: '16px', borderRadius: '4px', marginBottom: '16px', overflowX: 'auto', minHeight: '200px' }}>
					{loading && (
						<div style={{ textAlign: 'center', padding: '40px' }}>
							<Spinner />
							<p style={{ marginTop: '12px', color: '#646970' }}>Loading actual prompt from PromptBuilder...</p>
						</div>
					)}
					{error && (
						<div style={{ color: '#dc3232', padding: '12px' }}>
							<strong>Error:</strong> {error}
						</div>
					)}
					{!loading && !error && (
						<pre style={{ margin: 0, whiteSpace: 'pre-wrap', wordWrap: 'break-word' }}>
{prompt}
						</pre>
					)}
				</div>

				<div style={{ background: '#f0f6fc', padding: '12px', borderRadius: '4px', borderLeft: `4px solid ${variant.color}` }}>
					<strong style={{ display: 'block', marginBottom: '8px', fontSize: '13px' }}>
						 Included in {variant.name}:
					</strong>
					<ul style={{ margin: 0, paddingLeft: '20px', fontSize: '12px', lineHeight: '1.8' }}>
						{variant.included?.map((item, index) => (
							<li key={index}>{item}</li>
						))}
					</ul>
				</div>
			</div>

			<div style={{ marginTop: '20px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', gap: '12px' }}>
				<div style={{ fontSize: '12px', color: '#646970' }}>
					Switch variants in settings to compare
				</div>
				<Button variant="primary" onClick={onClose}>
					Close
				</Button>
			</div>
		</Modal>
	);
};

export default PromptPreviewModal;
