const PresetCard = ({ preset, presetId, isActive, onApply }) => {
	return (
		<button
			onClick={() => onApply(presetId)}
			style={{
				padding: '16px 12px',
				border: isActive ? `2px solid ${preset.color}` : '1px solid #dcdcde',
				borderRadius: '6px',
				background: isActive ? `${preset.color}10` : '#fff',
				cursor: 'pointer',
				textAlign: 'center',
				transition: 'all 0.2s'
			}}
		>
			<div style={{ fontSize: '32px', marginBottom: '8px' }}>{preset.icon}</div>
			<div style={{
				fontSize: '13px',
				fontWeight: '600',
				marginBottom: '4px',
				color: isActive ? preset.color : '#1d2327'
			}}>
				{preset.name}
			</div>
			<div style={{ fontSize: '11px', color: '#646970', lineHeight: '1.4' }}>
				{preset.description}
			</div>
			{isActive && (
				<div style={{
					marginTop: '8px',
					padding: '4px 8px',
					background: preset.color,
					color: '#fff',
					borderRadius: '3px',
					fontSize: '10px',
					fontWeight: 'bold',
					textTransform: 'uppercase'
				}}>
					Active
				</div>
			)}
		</button>
	);
};

export default PresetCard;
