import { useState } from '@wordpress/element';
import { Card, CardHeader, CardBody } from '@wordpress/components';
import VariantCard from '../shared/VariantCard';
import PromptPreviewModal from '../shared/PromptPreviewModal';
import { VARIANT_CONFIG } from '../constants/variantConfig';

const PromptTab = ({ settings, setSettings }) => {
	const [showPreview, setShowPreview] = useState(false);
	const [previewVariant, setPreviewVariant] = useState('standard');
	const selectedVariant = settings.prompt_variant || 'standard';

	const handleShowPreview = (variant) => {
		setPreviewVariant(variant);
		setShowPreview(true);
	};

	return (
		<div>
			<Card>
				<CardHeader>
					<h3>Prompt Detail Level</h3>
				</CardHeader>
				<CardBody>
					<p style={{ marginBottom: '20px', color: '#646970' }}>
						Control how much text context is sent to the AI. More context = better quality but higher cost.
						<br />
						<strong>Note:</strong> Image file size adds ~1,500-2,500 tokens regardless of variant selected.
					</p>

					{/* 3 Column Variant Selection */}
					<div style={{ display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)', gap: '16px', marginBottom: '30px' }}>
						{/* Minimal */}
						<label
							style={{
								display: 'flex',
								flexDirection: 'column',
								padding: '16px',
								border: selectedVariant === 'minimal' ? '3px solid #d63638' : '2px solid #dcdcde',
								borderRadius: '8px',
								cursor: 'pointer',
								backgroundColor: selectedVariant === 'minimal' ? '#fff5f5' : 'white',
								transition: 'all 0.2s'
							}}
						>
							<input
								type="radio"
								value="minimal"
								checked={selectedVariant === 'minimal'}
								onChange={(e) => setSettings({ ...settings, prompt_variant: e.target.value })}
								style={{ marginBottom: '12px' }}
							/>
							<div style={{
								textAlign: 'center',
								marginBottom: '12px'
							}}>
								<span style={{
									padding: '4px 12px',
									borderRadius: '4px',
									fontSize: '12px',
									fontWeight: 700,
									backgroundColor: '#d63638',
									color: 'white',
									textTransform: 'uppercase',
									display: 'inline-block'
								}}> Minimal</span>
							</div>
							<div style={{ textAlign: 'center', marginBottom: '8px' }}>
								<div style={{ fontSize: '13px', fontWeight: 600, marginBottom: '4px' }}>
									{VARIANT_CONFIG.minimal.description}
								</div>
								<div style={{ fontSize: '12px', color: '#646970' }}>
									{VARIANT_CONFIG.minimal.promptTokens} text tokens
								</div>
							</div>
						</label>

						{/* Standard */}
						<label
							style={{
								display: 'flex',
								flexDirection: 'column',
								padding: '16px',
								border: selectedVariant === 'standard' ? '3px solid #2271b1' : '2px solid #dcdcde',
								borderRadius: '8px',
								cursor: 'pointer',
								backgroundColor: selectedVariant === 'standard' ? '#f0f6fc' : 'white',
								transition: 'all 0.2s'
							}}
						>
							<input
								type="radio"
								value="standard"
								checked={selectedVariant === 'standard'}
								onChange={(e) => setSettings({ ...settings, prompt_variant: e.target.value })}
								style={{ marginBottom: '12px' }}
							/>
							<div style={{
								textAlign: 'center',
								marginBottom: '12px'
							}}>
								<span style={{
									padding: '4px 12px',
									borderRadius: '4px',
									fontSize: '12px',
									fontWeight: 700,
									backgroundColor: '#2271b1',
									color: 'white',
									textTransform: 'uppercase',
									display: 'inline-block'
								}}> Standard</span>
							</div>
							<div style={{ textAlign: 'center', marginBottom: '8px' }}>
								<div style={{ fontSize: '13px', fontWeight: 600, marginBottom: '4px' }}>
									{VARIANT_CONFIG.standard.description}
								</div>
								<div style={{ fontSize: '12px', color: '#646970' }}>
									{VARIANT_CONFIG.standard.promptTokens} text tokens
								</div>
							</div>
						</label>

						{/* Advanced */}
						<label
							style={{
								display: 'flex',
								flexDirection: 'column',
								padding: '16px',
								border: selectedVariant === 'advanced' ? '3px solid #00a32a' : '2px solid #dcdcde',
								borderRadius: '8px',
								cursor: 'pointer',
								backgroundColor: selectedVariant === 'advanced' ? '#f0f9f4' : 'white',
								transition: 'all 0.2s'
							}}
						>
							<input
								type="radio"
								value="advanced"
								checked={selectedVariant === 'advanced'}
								onChange={(e) => setSettings({ ...settings, prompt_variant: e.target.value })}
								style={{ marginBottom: '12px' }}
							/>
							<div style={{
								textAlign: 'center',
								marginBottom: '12px'
							}}>
								<span style={{
									padding: '4px 12px',
									borderRadius: '4px',
									fontSize: '12px',
									fontWeight: 700,
									backgroundColor: '#00a32a',
									color: 'white',
									textTransform: 'uppercase',
									display: 'inline-block'
								}}> Advanced</span>
							</div>
							<div style={{ textAlign: 'center', marginBottom: '8px' }}>
								<div style={{ fontSize: '13px', fontWeight: 600, marginBottom: '4px' }}>
									{VARIANT_CONFIG.advanced.description}
								</div>
								<div style={{ fontSize: '12px', color: '#646970' }}>
									{VARIANT_CONFIG.advanced.promptTokens} text tokens
								</div>
							</div>
						</label>
					</div>
				</CardBody>
			</Card>

			{/* Variant Details - 3 Columns */}
			<div style={{ display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)', gap: '20px', marginTop: '20px' }}>
				<VariantCard
					variant={VARIANT_CONFIG.minimal}
					onShowPreview={() => handleShowPreview('minimal')}
				/>
				<VariantCard
					variant={VARIANT_CONFIG.standard}
					onShowPreview={() => handleShowPreview('standard')}
				/>
				<VariantCard
					variant={VARIANT_CONFIG.advanced}
					onShowPreview={() => handleShowPreview('advanced')}
				/>
			</div>

			{/* Preview Modal */}
			<PromptPreviewModal
				isOpen={showPreview}
				onClose={() => setShowPreview(false)}
				variant={VARIANT_CONFIG[previewVariant]}
				settings={settings}
				selectedVariant={previewVariant}
			/>
		</div>
	);
};

export default PromptTab;
