import { Button } from '@wordpress/components';

const TabsContainer = ({
	tabs,
	activeTab,
	onTabChange,
	onSave,
	saving = false,
	disabled = false,
	disabledTitle = ''
}) => {
	return (
		<>
			<style>
				{`
					.ai-media-settings-tab {
						background: transparent;
						border: none;
						border-bottom: 2px solid transparent;
						padding: 12px 20px;
						font-size: 14px;
						font-weight: 500;
						color: #646970;
						cursor: pointer;
						transition: all 0.2s ease;
					}
					.ai-media-settings-tab:hover {
						color: #2271b1;
						background: #f0f6fc;
					}
					.ai-media-settings-tab.active {
						color: #2271b1;
						border-bottom-color: #2271b1;
						font-weight: 600;
					}
				`}
			</style>

			<div className="ai-media-settings-header" style={{
				display: 'flex',
				alignItems: 'center',
				justifyContent: 'space-between',
				borderBottom: '1px solid #dcdcde'
			}}>
				<div className="ai-media-settings-tabs" style={{ display: 'flex' }}>
					{tabs.map((tab) => (
						<button
							key={tab.id}
							className={`ai-media-settings-tab ${activeTab === tab.id ? 'active' : ''}`}
							onClick={() => onTabChange(tab.id)}
						>
							{tab.label}
						</button>
					))}
				</div>

				<div style={{ paddingRight: '20px' }}>
					<Button
						variant="primary"
						onClick={onSave}
						isBusy={saving}
						disabled={disabled || saving}
						title={disabledTitle}
					>
						{saving ? 'Saving...' : 'Save Settings'}
					</Button>
				</div>
			</div>
		</>
	);
};

export default TabsContainer;
