import { Card, CardHeader, CardBody } from '@wordpress/components';

const QualityScorePreview = ({ scenarios, qualityWeights, autoApproveThreshold, calculateScore }) => {
	return (
		<Card style={{ marginBottom: '20px', position: 'sticky', top: '20px' }}>
			<CardHeader>
				<h3 style={{ margin: 0, display: 'flex', alignItems: 'center', gap: '8px' }}>
					<span className="dashicons dashicons-visibility" style={{ fontSize: '20px' }}></span>
					Quality Score Preview
				</h3>
			</CardHeader>
			<CardBody>
				{/* Threshold indicator */}
				<div style={{
					marginTop: 0,
					marginBottom: '16px',
					padding: '12px',
					background: '#f0f6fc',
					borderRadius: '4px',
					border: '1px solid #2271b1',
					fontSize: '12px',
					textAlign: 'center'
				}}>
					<strong>Auto-Approve Threshold:</strong> {Math.round(autoApproveThreshold * 100)}%
				</div>

				<p style={{ marginTop: 0, marginBottom: '16px', fontSize: '13px', color: '#646970' }}>
				See how quality affects your total score with current importance settings:
				</p>
				<div style={{
					padding: '8px 12px',
					background: '#f0f6fc',
					borderRadius: '4px',
					fontSize: '11px',
					color: '#646970',
					marginBottom: '16px',
					borderLeft: '3px solid #2271b1'
				}}>
					<strong> How to read:</strong><br/>
					<span style={{ color: '#1d2327' }}>If ALT is perfect (100%)</span>  adds <strong>45%</strong> to total score<br/>
					<span style={{ color: '#1d2327' }}>If ALT is good (75%)</span>  adds <strong>34%</strong> to total score
				</div>
				{Object.entries(scenarios).map(([id, scenario]) => {
					const score = calculateScore(scenario);
					const percentage = Math.round(score * 100);
					const threshold = autoApproveThreshold * 100;
					const willAutoApprove = percentage >= threshold;

					let statusColor = '#dc3232';
					let statusLabel = 'Draft';
					if (score >= autoApproveThreshold) {
						statusColor = '#46b450';
						statusLabel = 'Auto-Approve';
					} else if (score >= 0.70) {
						statusColor = '#ffb900';
						statusLabel = 'Review';
					}

					return (
						<div key={id} style={{
							marginBottom: '16px',
							padding: '12px',
							background: '#f6f7f7',
							borderRadius: '4px',
							border: willAutoApprove ? `2px solid ${statusColor}` : '1px solid #dcdcde'
						}}>
							<div style={{
								display: 'flex',
								justifyContent: 'space-between',
								alignItems: 'center',
								marginBottom: '8px'
							}}>
								<div style={{
									fontSize: '13px',
									fontWeight: '600',
									display: 'flex',
									alignItems: 'center',
									gap: '6px'
								}}>
									<span style={{ fontSize: '16px' }}>{scenario.emoji}</span>
									{scenario.label}
								</div>
								<div style={{
									fontSize: '16px',
									fontWeight: 'bold',
									color: statusColor
								}}>
									{percentage}%
								</div>
							</div>

							{/* Progress bar */}
							<div style={{
								height: '6px',
								background: '#dcdcde',
								borderRadius: '3px',
								overflow: 'hidden',
								marginBottom: '8px'
							}}>
								<div style={{
									height: '100%',
									width: `${percentage}%`,
									background: statusColor,
									transition: 'width 0.3s'
								}} />
							</div>

							{/* Field scores breakdown */}
							<div style={{
								fontSize: '11px',
								color: '#646970',
								display: 'grid',
								gridTemplateColumns: '1fr 1fr',
								gap: '4px'
							}}>
								{Object.entries(scenario).filter(([key]) => key !== 'label' && key !== 'emoji').map(([field, fieldScore]) => (
									<div key={field}>
									{field.toUpperCase()}: {Math.round(fieldScore * 100)}%  <span style={{ color: '#2271b1', fontWeight: '600' }}>+{Math.round(fieldScore * qualityWeights[field] * 100)}%</span>
									</div>
								))}
							</div>

							{/* Status badge */}
							<div style={{
								marginTop: '8px',
								padding: '4px 8px',
								background: statusColor,
								color: '#fff',
								borderRadius: '3px',
								fontSize: '11px',
								fontWeight: 'bold',
								textAlign: 'center',
								textTransform: 'uppercase'
							}}>
								 {statusLabel}
							</div>
						</div>
					);
				})}
			</CardBody>
		</Card>
	);
};

export default QualityScorePreview;
