const QualityImportanceSlider = ({ field, value, onChange, labels }) => {
	const label = labels[field];
	const importanceValue = Math.round(value); // 0-100 range

	return (
		<div style={{ marginBottom: '24px' }}>
			<div style={{
				display: 'flex',
				justifyContent: 'space-between',
				alignItems: 'center',
				marginBottom: '8px'
			}}>
				<label style={{
					fontSize: '14px',
					fontWeight: '600',
					display: 'flex',
					alignItems: 'center',
					gap: '8px'
				}}>
					<span style={{ fontSize: '18px' }}>{label.emoji}</span>
					{label.name} Importance
					<span style={{
						fontSize: '10px',
						padding: '2px 6px',
						background: `${label.color}20`,
						color: label.color,
						borderRadius: '3px',
						fontWeight: 'bold',
						textTransform: 'uppercase'
					}}>
						{label.importance}
					</span>
				</label>
				<div style={{
					display: 'flex',
					alignItems: 'center',
					gap: '8px'
				}}>
					<input
						type="number"
						min="0"
						max="100"
						step="1"
						value={importanceValue}
						onChange={(e) => {
							const newValue = Math.min(100, Math.max(0, parseInt(e.target.value) || 0));
							onChange(newValue);
						}}
						style={{
							width: '60px',
							padding: '4px 8px',
							border: '1px solid #8c8f94',
							borderRadius: '2px',
							fontSize: '13px',
							textAlign: 'right'
						}}
					/>
					<span style={{
						fontSize: '11px',
						fontWeight: '600',
						color: '#646970',
						minWidth: '35px'
					}}>points</span>
				</div>
			</div>

			<input
				type="range"
				min="0"
				max="100"
				step="1"
				value={importanceValue}
				onChange={(e) => {
					const newValue = parseInt(e.target.value);
					onChange(newValue);
				}}
				style={{
					width: '100%',
					height: '8px',
					borderRadius: '4px',
					outline: 'none',
					WebkitAppearance: 'none',
					appearance: 'none',
					background: `linear-gradient(to right, ${label.color} 0%, ${label.color} ${importanceValue}%, #dcdcde ${importanceValue}%, #dcdcde 100%)`
				}}
			/>

			<div style={{
				marginTop: '6px',
				fontSize: '11px',
				color: '#646970',
				fontStyle: 'italic'
			}}>
				Higher values = more important for overall quality score
			</div>
		</div>
	);
};

export default QualityImportanceSlider;
