/**
 * ImageSelector Component
 *
 * Multi-mode image selector for Playground:
 * - Use currently selected image (from Library context)
 * - Quick picks (5 recent images as thumbnails)
 * - Search (debounced autocomplete)
 * - Direct ID input (power users)
 */

import { useState, useEffect } from '@wordpress/element';
import { TextControl, Button, Spinner } from '@wordpress/components';
import apiFetch from '@wordpress/api-fetch';
import { useDebounce } from '../../hooks/useDebounce';
import ImageThumbnail from './ImageThumbnail';
import SearchResultItem from './SearchResultItem';

const ImageSelector = ({
	onSelect,
	selectedImage = null,
	preselectedImageId = null, // From Library context
	autoLoadPreselected = false
}) => {
	const [searchTerm, setSearchTerm] = useState('');
	const [searchResults, setSearchResults] = useState([]);
	const [quickPicks, setQuickPicks] = useState([]);
	const [quickPicksOffset, setQuickPicksOffset] = useState(0);
	const [hasMoreQuickPicks, setHasMoreQuickPicks] = useState(true);
	const [loading, setLoading] = useState({
		search: false,
		quick: false,
		loadMore: false
	});
	const [error, setError] = useState(null);

	const debouncedSearch = useDebounce(searchTerm, 300);
	const QUICK_PICKS_LIMIT = 12;

	// Load quick picks on mount
	useEffect(() => {
		loadQuickPicks();
	}, []);

	// Auto-search when debounced value changes
	useEffect(() => {
		if (debouncedSearch.length >= 2) {
			searchImages(debouncedSearch);
		} else {
			setSearchResults([]);
		}
	}, [debouncedSearch]);

	// Auto-load preselected image from Library context
	useEffect(() => {
		if (autoLoadPreselected && preselectedImageId) {
			handleSelectImage({ id: preselectedImageId });
		}
	}, [preselectedImageId, autoLoadPreselected]);

	const loadQuickPicks = async (append = false) => {
		const loadingKey = append ? 'loadMore' : 'quick';
		setLoading(prev => ({ ...prev, [loadingKey]: true }));
		setError(null);

		const offset = append ? quickPicksOffset : 0;

		try {
			const response = await apiFetch({
				path: `/ai-media/v1/playground/images?limit=${QUICK_PICKS_LIMIT}&offset=${offset}`,
				method: 'GET',
			});

			if (response.success) {
				const newImages = response.images || [];

				if (append) {
					setQuickPicks(prev => [...prev, ...newImages]);
				} else {
					setQuickPicks(newImages);
				}

				// Update offset for next load
				setQuickPicksOffset(offset + newImages.length);

				// Check if there are more images to load
				setHasMoreQuickPicks(newImages.length === QUICK_PICKS_LIMIT);
			}
		} catch (err) {
			setError('Failed to load recent images: ' + err.message);
		} finally {
			setLoading(prev => ({ ...prev, [loadingKey]: false }));
		}
	};

	const handleLoadMore = () => {
		loadQuickPicks(true);
	};

	const searchImages = async (query) => {
		setLoading(prev => ({ ...prev, search: true }));
		setError(null);

		try {
			const response = await apiFetch({
				path: `/ai-media/v1/playground/search?q=${encodeURIComponent(query)}&limit=10`,
				method: 'GET',
			});

			if (response.success) {
				setSearchResults(response.images || []);
			}
		} catch (err) {
			setError('Search failed: ' + err.message);
			setSearchResults([]);
		} finally {
			setLoading(prev => ({ ...prev, search: false }));
		}
	};

	const handleSelectImage = (image) => {
		onSelect(image);
		// Clear search after selection
		setSearchTerm('');
		setSearchResults([]);
	};

	return (
		<div className="image-selector" style={{
			display: 'flex',
			flexDirection: 'column',
			gap: '24px'
		}}>
			{/* Context-Aware Quick Select */}
			{preselectedImageId && (
				<div style={{
					padding: '12px',
					backgroundColor: '#f0f6fc',
					border: '1px solid #0783be',
					borderRadius: '4px'
				}}>
					<div style={{
						display: 'flex',
						alignItems: 'center',
						justifyContent: 'space-between',
						gap: '12px'
					}}>
						<div style={{ flex: 1 }}>
							<div style={{
								fontSize: '12px',
								color: '#0783be',
								fontWeight: 600,
								marginBottom: '4px',
								textTransform: 'uppercase',
								letterSpacing: '0.3px'
							}}>
								Quick Access
							</div>
							<div style={{ fontSize: '13px', color: '#1e1e1e' }}>
								Use the image you selected in Media Library
							</div>
						</div>
						<Button
							variant="primary"
							onClick={() => handleSelectImage({ id: preselectedImageId })}
							style={{
								whiteSpace: 'nowrap'
							}}
						>
							 Use Selected Image (ID: {preselectedImageId})
						</Button>
					</div>
				</div>
			)}

			{/* Quick Picks - Recent Images */}
			<div className="quick-picks-section">
				<h4 style={{
					margin: '0 0 12px 0',
					fontSize: '14px',
					fontWeight: 600,
					color: '#1e1e1e'
				}}>
					Recent Uploads
				</h4>
				{loading.quick ? (
					<div style={{ padding: '20px', textAlign: 'center' }}>
						<Spinner />
					</div>
				) : (
					<>
						<div className="thumbnail-grid" style={{
							display: 'grid',
							gridTemplateColumns: 'repeat(auto-fill, minmax(100px, 1fr))',
							gap: '10px'
						}}>
							{quickPicks.map(img => (
								<ImageThumbnail
									key={img.id}
									image={img}
									onClick={handleSelectImage}
									isSelected={selectedImage?.id === img.id}
								/>
							))}
						</div>

						{/* Load More Button */}
						{hasMoreQuickPicks && quickPicks.length > 0 && (
							<div style={{ marginTop: '16px', textAlign: 'center' }}>
								<Button
									variant="secondary"
									onClick={handleLoadMore}
									disabled={loading.loadMore}
									isBusy={loading.loadMore}
								>
									{loading.loadMore ? 'Loading...' : 'Load More'}
								</Button>
								<p style={{
									margin: '8px 0 0 0',
									fontSize: '12px',
									color: '#646970'
								}}>
									Showing {quickPicks.length} images
								</p>
							</div>
						)}
					</>
				)}
			</div>

			{/* Search */}
			<div className="search-section">
				<h4 style={{
					margin: '0 0 12px 0',
					fontSize: '14px',
					fontWeight: 600,
					color: '#1e1e1e'
				}}>
					Search Images
				</h4>
				<div style={{ position: 'relative' }}>
					<TextControl
						placeholder="Search by filename, title, or ID..."
						value={searchTerm}
						onChange={setSearchTerm}
						help="Start typing to search (minimum 2 characters)"
					/>
					{loading.search && (
						<div style={{
							position: 'absolute',
							right: '8px',
							top: '8px'
						}}>
							<Spinner style={{ margin: 0 }} />
						</div>
					)}
				</div>

				{searchResults.length > 0 && (
					<div className="search-results" style={{
						marginTop: '12px',
						maxHeight: '300px',
						overflowY: 'auto',
						border: '1px solid #dcdcde',
						borderRadius: '4px',
						padding: '8px',
						backgroundColor: '#f6f7f7'
					}}>
						{searchResults.map(img => (
							<SearchResultItem
								key={img.id}
								image={img}
								onClick={handleSelectImage}
								isSelected={selectedImage?.id === img.id}
							/>
						))}
					</div>
				)}

				{debouncedSearch.length >= 2 && !loading.search && searchResults.length === 0 && (
					<div style={{
						marginTop: '12px',
						padding: '12px',
						textAlign: 'center',
						color: '#646970',
						fontSize: '13px',
						backgroundColor: '#f6f7f7',
						borderRadius: '4px'
					}}>
						No images found for "{debouncedSearch}"
					</div>
				)}
			</div>

			{/* Error Display */}
			{error && (
				<div style={{
					padding: '12px',
					backgroundColor: '#fcf0f1',
					border: '1px solid #dc3232',
					borderRadius: '4px',
					color: '#dc3232',
					fontSize: '13px'
				}}>
					{error}
				</div>
			)}
		</div>
	);
};

export default ImageSelector;
