/**
 * Hook for Playground tab functionality
 *
 * Manages state and API calls for AI context debugging and testing.
 */

import { useState, useCallback } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

/**
 * Manage playground state and provide debugging utilities
 * @returns {Object} Playground state and utilities
 */
export const usePlayground = () => {
	const [selectedImage, setSelectedImage] = useState(null);
	const [context, setContext] = useState(null);
	const [analysisResult, setAnalysisResult] = useState(null);
	const [loading, setLoading] = useState({
		context: false,
		analysis: false,
	});
	const [error, setError] = useState(null);

	/**
	 * Load context for selected image
	 * @param {number} attachmentId - Attachment ID
	 * @param {string} language - Language code (optional)
	 * @param {string} variantOverride - Optional prompt variant override
	 */
	const loadContext = useCallback(async (attachmentId, language, variantOverride = null) => {
		setLoading(prev => ({ ...prev, context: true }));
		setError(null);
		setContext(null);
		setAnalysisResult(null); // Clear previous analysis

		try {
			const requestData = {
				attachment_id: attachmentId,
			};

			if (language) {
				requestData.language = language;
			}

			if (variantOverride) {
				requestData.prompt_variant = variantOverride;
			}

			const response = await apiFetch({
				path: '/ai-media/v1/playground/context',
				method: 'POST',
				data: requestData,
			});

			setContext(response);
		} catch (err) {
			setError('Failed to load context: ' + err.message);
		} finally {
			setLoading(prev => ({ ...prev, context: false }));
		}
	}, []);

	/**
	 * Analyze image with AI
	 * @param {number} attachmentId - Attachment ID
	 * @returns {Promise<Object>} Analysis result
	 */
	const analyzeImage = useCallback(async (attachmentId) => {
		setLoading(prev => ({ ...prev, analysis: true }));
		setError(null);

		try {
			const response = await apiFetch({
				path: '/ai-media/v1/analyze',
				method: 'POST',
				data: {
					attachment_ids: [attachmentId],
				},
			});

			if (response.success && response.results && response.results[0]) {
				setAnalysisResult(response.results[0]);
				return response.results[0];
			} else if (response.errors && response.errors[0]) {
				throw new Error(response.errors[0].errors[0] || 'Analysis failed');
			} else {
				throw new Error('No results returned from analysis');
			}
		} catch (err) {
			setError('Failed to analyze image: ' + err.message);
			throw err;
		} finally {
			setLoading(prev => ({ ...prev, analysis: false }));
		}
	}, []);

	return {
		selectedImage,
		setSelectedImage,
		context,
		analysisResult,
		loading,
		error,
		loadContext,
		analyzeImage,
	};
};
